import { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import { InterestsHobbies } from "./InterestsHobbies";
import 'animate.css';

export const Banner = () => {
  const [loopNum, setLoopNum] = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  const [text, setText] = useState('');
  const [delta, setDelta] = useState(300 - Math.random() * 100);
  const [, setIndex] = useState(1);
  const toRotate = [ "Engineer", "Developer" ];
  const period = 2000;

  useEffect(() => {
    let ticker = setInterval(() => {
      tick();
    }, delta);

    return () => { clearInterval(ticker) };
  }, [text])

  const tick = () => {
    let i = loopNum % toRotate.length;
    let fullText = toRotate[i];
    let updatedText = isDeleting ? fullText.substring(0, text.length - 1) : fullText.substring(0, text.length + 1);

    setText(updatedText);

    if (isDeleting) {
      setDelta(prevDelta => prevDelta / 2);
    }

    if (!isDeleting && updatedText === fullText) {
      setIsDeleting(true);
      setIndex(prevIndex => prevIndex - 1);
      setDelta(period);
    } else if (isDeleting && updatedText === '') {
      setIsDeleting(false);
      setLoopNum(loopNum + 1);
      setIndex(1);
      setDelta(500);
    } else {
      setIndex(prevIndex => prevIndex + 1);
    }
  }

  return (
    <section className="banner" id="home">
      <Container>
        <Row className="text aligh-items-center">
          <Col xs={12} md={6} xl={7}>
              <div className={"info animate__animated animate__backInLeft"}>
                <span className={"tagline animate__animated animate__fadeIn"} >Welcome to my Portfolio</span>
                <h1>Hi! I'm José Miguel, a Software <span className="txt-rotate" dataPeriod="1000" data-rotate='[ "Engineer", "Developer" ]'><span className="wrap">{text}</span></span></h1>
                <p>At this time, I am completing my bachelor's degree in Informatics and Computing Engineering at <a href="https://sigarra.up.pt/feup/pt/web_page.inicial">FEUP</a>.</p>
                {/*<p>I am a dedicated Software Engineering student based in Porto, fueled by a profound passion for programming and software development. My interests extend to the realms of artificial intelligence and cybersecurity, encompassing the vast landscape of software engineering. Beyond the coding screen, I am an avid sports enthusiast, with a particular love for football and tennis. In my leisure time, I find joy in gaming and maintaining an active lifestyle through regular exercise.</p>*/} 
              </div>
          </Col>
        </Row>
        <Row className="tabs aligh-items-center">
          <Col xs={10} md={4} xl={6}>
            <div className={"interestshobbies animate__animated animate__fadeInUp"}>
              <InterestsHobbies />
            </div>
          </Col>
        </Row>
      </Container>
    </section>
  )
}
