import { useState, useEffect } from "react";
import { DarkModeSwitch } from "react-toggle-dark-mode";

export const DarkMode = () => {
  const [isDarkMode, setDarkMode] = useState(false);

  // Add or remove the "dark" class on the document body
  const toggleDarkMode = (checked) => {
    setDarkMode(checked);
    document.body.classList.toggle('dark', checked);
  };

  useEffect(() => {
    // Use matchMedia to check the user preference
    const prefersDark = window.matchMedia('(prefers-color-scheme: dark)');

    toggleDarkMode(prefersDark.matches);

    // Listen for changes to the prefers-color-scheme media query
    prefersDark.addEventListener('change', (mediaQuery) => toggleDarkMode(mediaQuery.matches));
  }, []);

  return (
    <DarkModeSwitch checked={isDarkMode} onChange={toggleDarkMode} size={38} moonColor="white" sunColor="white"/>
  );
};