import { useState, useEffect } from "react";
import { Navbar, Nav, Container } from "react-bootstrap";
import logo from '../assets/img/logo.png';
import githubIcon from '../assets/img/github-icon.svg';
import linkeddinIcon from '../assets/img/linkedin-icon.svg';
import {
  BrowserRouter as Router
} from "react-router-dom";
import { DarkMode } from "./DarkMode";

export const NavBar = () => {

  const [activeLink, setActiveLink] = useState('home');
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const onScroll = () => {
      if (window.scrollY > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    }

    window.addEventListener("scroll", onScroll);

    return () => window.removeEventListener("scroll", onScroll);
  }, [])

  const onUpdateActiveLink = (value) => {
    setActiveLink(value);
  }
  const copyToClipboard = () => {
    var copyText = "josemiguelisidro@gmail.com"; 
    navigator.clipboard.writeText(copyText);
  
    var tooltip = document.getElementById("navSlide");
    tooltip.setAttribute("data-replace", "Copied");
  }

  const resetToolTip = () => {  
    var tooltip = document.getElementById("navSlide");
    tooltip.setAttribute("data-replace", "Copy Email");
  }

  return (
    <Router>
      <Navbar expand="md" className={scrolled ? "scrolled" : ""}>
        <Container className="animate__animated animate__bounceInDown">
          <Navbar.Brand href="/">
            <img src={logo} alt="Logo" />
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav">
            <span className="navbar-toggler-icon"></span>
          </Navbar.Toggle>
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ms-auto">
              <Nav.Link href="#home" className={activeLink === 'home' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('home')}>Home</Nav.Link>
              <Nav.Link href="#skills" className={activeLink === 'skills' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('skills')}>Skills</Nav.Link>
              <Nav.Link href="#projects" className={activeLink === 'projects' ? 'active navbar-link' : 'navbar-link'} onClick={() => onUpdateActiveLink('projects')}>Projects</Nav.Link>
            </Nav>
            <span className="navbar-text">
              <div className="social-icon">
                <DarkMode />
                <a href="https://www.linkedin.com/in/jose-miguel-isidro"><img src={linkeddinIcon} alt="LinkedIn Profile" /></a>
                <a href="https://github.com/zmiguel2011"><img src={githubIcon} alt="GitHub Profile" /></a>
              </div>
                <button onClick={() => copyToClipboard()} onMouseOut={() => setTimeout(resetToolTip, 300)} >
                  <div className="textslide"  id="navSlide" data-replace="Copy Email">
                    <span className="replacetext">Let's Connect</span> 
                  </div>
                </button>
            </span>
          </Navbar.Collapse>
        </Container>
      </Navbar>
    </Router>
  )
}
